/* Admin Orders - Read-Only View */

.admin-orders-container {
  padding: 24px 28px;
}

.page-header {
  margin-bottom: 24px;
}

.page-title {
  font-size: 28px;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0 0 6px 0;
}

.page-subtitle {
  font-size: 14px;
  color: var(--text-secondary);
  margin: 0;
}

.orders-table-wrapper {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: 12px;
  overflow: hidden;
}

.orders-table {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
}

.orders-table thead {
  background: var(--bg-hover);
  border-bottom: 1px solid var(--divider);
  position: sticky;
  top: 0;
  z-index: 1;
}

.orders-table th {
  text-align: left;
  padding: 14px 16px;
  font-size: 13px;
  font-weight: 700;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.orders-table tbody tr {
  border-bottom: 1px solid var(--divider);
  transition: background 140ms ease;
}

.orders-table tbody tr:last-child {
  border-bottom: none;
}

.orders-table tbody tr:hover {
  background: var(--bg-hover);
}

.orders-table td {
  padding: 16px;
  font-size: 14px;
  color: var(--text-primary);
}

.order-id-cell {
  font-weight: 700;
  color: var(--text-primary);
  width: 18%;
}

.vendor-name-cell {
  color: var(--text-primary);
  width: 40%;
}

.value-cell {
  font-weight: 700;
  color: var(--text-primary);
  width: 20%;
}

.status-cell {
  width: 22%;
}

.order-status-badge {
  display: inline-block;
  padding: 6px 14px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 700;
  text-transform: capitalize;
}

.order-status-badge.completed {
  background: color-mix(in srgb, var(--primary) 12%, transparent);
  color: var(--primary);
}

.order-status-badge.cancelled {
  background: color-mix(in srgb, var(--error) 12%, transparent);
  color: var(--error);
}
