/* Vendors Management Page Styles */

.vendors-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.vendors-title {
  font-size: 28px;
  font-weight: 700;
  margin: 0;
}

.vendors-loading {
  text-align: center;
  padding: 40px;
  color: var(--text-secondary);
}

.vendors-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
  margin-bottom: 24px;
}

.stat-card {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 16px;
}

.stat-label {
  font-size: 12px;
  color: var(--text-secondary);
  margin-bottom: 8px;
}

.stat-value {
  font-size: 24px;
  font-weight: 700;
}

.vendors-section {
  margin-bottom: 32px;
}

.vendors-table-wrap {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: 10px;
  overflow: hidden;
}

.vendors-table {
  width: 100%;
  border-collapse: collapse;
}

.vendors-table thead {
  background: var(--bg-hover);
}

.vendors-table th {
  padding: 12px 14px;
  text-align: left;
  font-weight: 600;
  font-size: 12px;
  color: var(--text-secondary);
  border-bottom: 1px solid var(--divider);
}

.vendors-table td {
  padding: 12px 14px;
  border-bottom: 1px solid var(--divider);
  color: var(--text-primary);
}

.vendors-table tbody tr:last-child td {
  border-bottom: none;
}

.vendors-table tbody tr:hover {
  background: var(--bg-hover);
}

.empty-state {
  text-align: center;
  color: var(--text-secondary);
  padding: 40px !important;
}

.status-badge {
  display: inline-block;
  font-size: 11px;
  padding: 4px 10px;
  border-radius: 999px;
  font-weight: 600;
  text-transform: uppercase;
}

.status-badge.active {
  background: color-mix(in srgb, var(--success) 12%, transparent);
  color: var(--success);
}

.status-badge.inactive {
  background: color-mix(in srgb, var(--text-secondary) 12%, transparent);
  color: var(--text-secondary);
}

.action-btns {
  display: flex;
  gap: 8px;
}

.btn-primary,
.btn-secondary,
.btn-edit,
.btn-delete {
  appearance: none;
  border: 1px solid var(--border);
  background: var(--primary);
  color: white;
  padding: 8px 14px;
  border-radius: 8px;
  font-weight: 500;
  cursor: pointer;
  font-size: 13px;
}

.btn-primary:hover {
  background: color-mix(in srgb, var(--primary) 85%, black);
}

.btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-secondary {
  background: transparent;
  color: var(--text-primary);
}

.btn-secondary:hover {
  background: var(--bg-hover);
}

.btn-edit {
  background: color-mix(in srgb, var(--info) 90%, white);
  color: white;
  border-color: var(--info);
}

.btn-edit:hover {
  background: var(--info);
}

.btn-delete {
  background: transparent;
  color: var(--error);
  border-color: var(--error);
}

.btn-delete:hover {
  background: color-mix(in srgb, var(--error) 12%, transparent);
}

/* Vendor Form */

.vendor-form {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 24px;
}

.form-section {
  margin-bottom: 32px;
}

.form-section:last-of-type {
  margin-bottom: 0;
}

.form-section-title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 16px;
  padding-bottom: 8px;
  border-bottom: 1px solid var(--divider);
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}

.form-field {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.form-field label {
  font-size: 13px;
  font-weight: 500;
  color: var(--text-secondary);
}

.form-field input,
.form-field select,
.form-field textarea {
  appearance: none;
  border: 1px solid var(--border);
  background: var(--bg-main);
  color: var(--text-primary);
  padding: 10px 12px;
  border-radius: 8px;
  font-size: 14px;
  font-family: inherit;
}

.form-field input:focus,
.form-field select:focus,
.form-field textarea:focus {
  outline: 2px solid color-mix(in srgb, var(--primary) 40%, transparent);
  outline-offset: 2px;
}

.form-field select {
  cursor: pointer;
}

.form-field textarea {
  resize: vertical;
}

.form-field-checkbox {
  flex-direction: row;
  align-items: center;
}

.form-field-checkbox label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  color: var(--text-primary);
}

.form-field-checkbox input[type='checkbox'] {
  width: 18px;
  height: 18px;
  cursor: pointer;
  margin: 0;
}

.form-field-full {
  grid-column: 1 / -1;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid var(--divider);
}

/* Responsive */

@media (max-width: 1024px) {
  .vendors-stats {
    grid-template-columns: repeat(2, 1fr);
  }
  .form-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .vendors-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }
  .vendors-stats {
    grid-template-columns: 1fr;
  }
  .vendors-table-wrap {
    overflow-x: auto;
  }
  .vendors-table {
    min-width: 600px;
  }
}
