.inventory-header {
  margin-bottom: 2rem;
}

.inventory-title {
  font-size: 2rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.inventory-loading {
  text-align: center;
  padding: 2rem;
  color: var(--text-secondary);
}

.inventory-controls {
  margin-bottom: 2rem;
  display: flex;
  gap: 1.5rem;
  align-items: center;
}

.filter-group {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.filter-group label {
  font-size: 0.95rem;
  font-weight: 500;
  color: var(--text-primary);
}

.vendor-filter {
  padding: 0.5rem 0.75rem;
  border: 1px solid var(--border-color);
  border-radius: 6px;
  background: var(--bg-secondary);
  color: var(--text-primary);
  font-size: 0.95rem;
  cursor: pointer;
  transition: border-color 0.2s;
}

.vendor-filter:hover {
  border-color: var(--primary-color);
}

.vendor-filter:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.inventory-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
}

.stat-card {
  background: var(--bg-secondary);
  padding: 1.25rem;
  border-radius: 8px;
  border: 1px solid var(--border-color);
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.stat-label {
  font-size: 0.85rem;
  font-weight: 500;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.stat-value {
  font-size: 2rem;
  font-weight: 700;
  color: var(--primary-color);
}

.inventory-section {
  margin-bottom: 2rem;
}

.section-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 1rem 0;
}

.inventory-table-wrap {
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  border-radius: 8px;
  overflow: hidden;
  overflow-x: auto;
}

.inventory-table {
  width: 100%;
  border-collapse: collapse;
}

.inventory-table thead {
  background: var(--bg-hover);
}

.inventory-table th {
  padding: 12px 14px;
  text-align: left;
  font-weight: 600;
  font-size: 12px;
  color: var(--text-secondary);
  border-bottom: 1px solid var(--border-color);
}

.inventory-table td {
  padding: 12px 14px;
  border-bottom: 1px solid var(--divider);
  color: var(--text-primary);
}

.inventory-table tbody tr:last-child td {
  border-bottom: none;
}

.inventory-table tbody tr:hover {
  background: var(--bg-hover);
}

.product-name-cell {
  display: flex;
  align-items: center;
  gap: 12px;
}

.product-thumb {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 6px;
  border: 1px solid var(--border-color);
}

.product-name {
  font-weight: 600;
  margin-bottom: 2px;
}

.product-desc {
  font-size: 12px;
  color: var(--text-secondary);
}

.stock-badge {
  display: inline-block;
  font-size: 11px;
  padding: 4px 10px;
  border-radius: 999px;
  font-weight: 600;
}

.stock-badge.good {
  background: color-mix(in srgb, var(--success) 12%, transparent);
  color: var(--success);
}

.stock-badge.low {
  background: color-mix(in srgb, var(--warning) 12%, transparent);
  color: var(--warning);
}

.stock-badge.out {
  background: color-mix(in srgb, var(--error) 12%, transparent);
  color: var(--error);
}

.status-badge {
  display: inline-block;
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 11px;
  font-weight: 600;
  letter-spacing: 0.3px;
}

.status-badge.active {
  background: color-mix(in srgb, var(--primary) 15%, transparent);
  color: var(--primary);
}

.status-badge.inactive {
  background: color-mix(in srgb, var(--text-secondary) 10%, transparent);
  color: var(--text-secondary);
}

.empty-state {
  text-align: center;
  padding: 2rem;
  color: var(--text-secondary);
  font-style: italic;
}

.action-btns {
  display: flex;
  gap: 8px;
  align-items: center;
}

.btn-edit,
.btn-delete {
  padding: 6px 12px;
  border-radius: 6px;
  border: 1px solid var(--border-color);
  background: transparent;
  font-size: 12px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-edit {
  background: transparent;
  color: var(--primary);
  border-color: var(--primary);
  border: 1px solid var(--primary);
}

.btn-edit:hover {
  background: color-mix(in srgb, var(--primary) 12%, transparent);
}

.btn-delete {
  background: transparent;
  color: var(--error);
  border-color: var(--error);
  border: 1px solid var(--warning);
}

.btn-delete:hover {
  background: color-mix(in srgb, var(--error) 12%, transparent);
}

/* Dark theme adjustments */
body.theme-dark .inventory-header,
body.theme-dark .inventory-controls {
  color: var(--text-primary);
}

body.theme-dark .vendor-filter {
  background: var(--bg-secondary);
  color: var(--text-primary);
  border-color: var(--border-color);
}

body.theme-dark .product-thumb {
  border-color: var(--border-color);
}
